/* Hero-Section */
section.hero-section {
  background-color: var(--light-color);
  margin: 2em;
  box-shadow: 0 0.4em 0.7em var(--background-color);
  border-radius: 0.2em;
}

div.hero-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  height: 70vh;
}

.hero-img-container {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.hero-img {
  position: relative;
  width: 100%;
  height: 100%;
  opacity: 0.4;
  object-fit: cover;
  object-position: center;
  animation: fade-in 1s ease-in-out forwards;
}

@keyframes fade-in {
  to {
    opacity: 1;
  }
}

.hero-text-container,
.hero-social-links-container {
  position: relative;
  z-index: 1;
  text-align: center;
}

.hero-text {
  color: var(--light-color);
  -webkit-text-stroke: 0.03em;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 0.3em 0.6em var(--text-color);
  transition: -webkit-text-fill-color, var(--slow-transition) linear;
  cursor: default;
  width: max(70%);
  margin: auto;
}

.hero-text:hover {
  -webkit-text-fill-color: var(--light-color);
}

.links-list {
  display: flex;
}

.links-list li {
  margin-top: 1em;
  margin-left: 2em;
}

.links-list li a {
  color: var(--light-color);
  transition: transform,
    var(--normal-transition) cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.links-list li a:hover {
  transform: translateY(-0.4em);
  display: inline-block;
}

div.hero-btn-container {
  position: absolute;
  bottom: 8em;
  z-index: 1;
}

.hero-btn {
  padding: 1em;
  margin: 1em;
  background-color: var(--text-color);
  color: var(--light-color);
  border: none;
  border-radius: 0.2em;
  opacity: 0.7;
  transition: opacity, letter-spacing, var(--slow-transition) ease-in-out;
}

.hero-btn:hover {
  display: inline-block;
  letter-spacing: 0.1em;
  opacity: 1;
}

div.hero-footer {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero-footer img {
  width: 15em;
  aspect-ratio: 16 / 9;
  z-index: 1;
  position: absolute;
  transition: opacity, transform, var(--slow-transition) ease-in-out;
  border: 0.1em solid var(--text-color);
}

.hero-footer-right {
  right: -2em;
  bottom: -2em;
  opacity: 0;
  animation: scale-up 1s ease-in-out forwards 0.5s;
}

.hero-footer-left {
  left: -2em;
  top: 8em;
  animation: scale-up 1s ease-in-out forwards;
}

.hero-footer > *:hover {
  border-radius: 4em;
  cursor: pointer;
}

@keyframes scale-up {
  0% {
    opacity: 0.4;
    transform: scale(0);
  }
  80% {
    opacity: 0.8;
    transform: scale(1);
  }
  100% {
    opacity: 1;
    transform: rotate(-8deg);
  }
}

/* Founder-section */
.founder-container {
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  width: max(90%, 80%);
  margin: auto;
  gap: 2em;
}

.founder-section h2 {
  margin-top: 1em;
}

.founder-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 0.4em;
  clip-path: circle(50% at center);
}

.founder-about-container {
  display: grid;
  place-items: center;
  box-shadow: 0 0.4em 0.7em var(--light-color);
  padding: 1em;
}

.founder-about-container h3 {
  padding: 0.4em;
}

.founder-about {
  text-align: center;
}

.founder-contact {
  border: 0.1em solid var(--text-color);
  padding: 0.3em 0.6em;
  background-color: var(--text-color);
  color: var(--light-color);
  border-radius: 0.1em;
  transition: border, var(--fast-transition) ease-in-out;
}

.founder-contact:hover {
  border: 0.1em solid var(--light-color);
  transform: scale(1.1);
}

/* Charity Section */
div.charity-container {
  /* display: grid;
  grid-template-columns: 1fr 1fr; */
  display: flex;
  justify-content: space-evenly;
  gap: 1em;
  padding: 1em;
  box-shadow: 0.3em 0.4em 0.8em var(--dark-rgb);
  position: relative;
}

/* .charity-bg-img {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0.2;
} */

.charity-img {
  width: 90%;
  height: 100%;
  -webkit-clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
  clip-path: polygon(0% 0%, 75% 0%, 100% 50%, 75% 100%, 0% 100%);
  border-radius: 0.4em;
}

.charity-text-container h3 {
  transform: translateX(-4em);
  padding: 1em;
}
