form.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: max(80%, 70%);
  margin: auto;
  padding: 1em;
}

.contact-title {
  text-align: center;
}

.contact-form input,
.contact-form textarea {
  border-radius: 0.2em;
  transition: box-shadow, var(--normal-transition) ease-in-out;
}

.contact-form input:hover,
.contact-form textarea:hover {
  box-shadow: 0 0.2em 0.5em var(--dark-rgb);
}

form input::placeholder,
form textarea::placeholder {
  transition: transform, var(--normal-transition) ease-in-out;
}

form input:hover::placeholder,
form textarea:hover::placeholder {
  transform: translateY(-0.3em);
}

.contact-form input,
.contact-form textarea,
.contact-form button {
  margin-top: 1em;
  padding: 1em;
  resize: none;
  border: none;
  cursor: pointer;
}

.form-btn {
  background-color: var(--text-color);
  color: var(--light-color);
  opacity: 0.8;
  transition: opacity,
    var(--fast-transiton) cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.form-btn:hover {
  opacity: 1;
}

.error-message {
  text-align: center;
  width: max(80%, 70%);
  margin: auto;
  padding: 1em;
  font-size: var(--nav-font);
  box-shadow: 0 0.3em 0.5em var(--light-rgb);
  background-color: var(--text-color);
  color: var(--light-color);
  font-size: var(--nav-font);
}

div.contact-extra-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
  justify-content: space-between;
  box-shadow: 0.2em 0.4em 0.8em var(--dark-rgb);
}

.contact-details-title {
  text-align: center;
}

.contact-extra-details {
  padding: 1em;
  width: max(70%, 60%);
  margin: auto;
}

.contact-extra-details > * {
  margin-top: 1em;
  margin-right: 1em;
  background-color: var(--light-color);
  padding: 1em;
  box-shadow: 0.2em 0.4em 0.8em var(--dark-rgb);
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-radius: 0.2em;
}

.contact-extra-details a {
  display: inline-block;
  border: 0.1em solid var(--dark-rgb);
  padding: 0.2em 0.4em;
  margin-top: 0.3em;
  background-color: var(--text-color);
  color: var(--light-color);
  text-transform: uppercase;
  transition: letter-spacing, var(--normal-transiton) ease-in-out;
}

.contact-extra-details a:hover {
  letter-spacing: 0.1em;
}

.contact-icon {
  font-size: clamp(2em, 3em, 5em);
  flex: 0.5;
}

.contact-icon + div {
  flex: 1;
  margin-right: 1em;
}
